import goods from "./goods.vue"
export default {
	data() {
		return {
			isOpen: false,
			isOpenitem: false
		};
	},
	components: {
		goods
	},
	props: {
		goodsinfo: {
			type: Object,
			default: {}
		},
	},
	filters: {
		filtertag(value) {
			if (value == 1) {
				return '自营'
			} else if (value == 2) {
				return '品牌'
			} else {
				return '基地'
			}
		}
	},
	methods: {
		tapOpen() {
			if (this.isOpen) {
				this.isOpenitem = false;
				this.$nextTick(() => {
					this.isOpen = false;
				})
			} else {
				this.isOpen = true;
				this.$nextTick(() => {
					this.isOpenitem = true
				})
			}
		},
	}
}
